      <!--<a href="http://prezi.com" target="_blank">prezi.com</a> <br/>-->
      <!-- <strong>CSS3</strong> </p>-->
      <!--</div>-->

      <!--<div id="big" class="step" data-x="3500" data-y="2100" data-rotate="180" data-scale="6">-->
      <!--<p> <big style="font-size:larger"></big> </p>-->
      <!--</div>-->

      <!--

          

          `data-x``data-y``data-z`
          `data-z="-3000"`3000px

      -->
      <!--<div id="tiny" class="step" data-x="2825" data-y="2325" data-z="-3000" data-rotate="300" data-scale="1">-->
      <!--<p> <span style="font-size:smaller"></span>  <span class="thoughts"></span></p>-->
      <!--</div>-->

      <!--

          data
          CSS transition

          step`future`

          `present`
          `present`

          `present``past`

          `future``present``past`
          step`present`

      -->
      <div
        id="ing"
        class="step"
        data-x="1500"
        data-y="-1850"
        data-rotate="0"
        data-scale="6"
      >
        <div class="step_one">
          <font color="#666"
            ><font color="#cc7832">const</font> person =
            <font color="#cc7832">new</font> Map()</font
          >
          <br />
          <font color="#666"
            >person.<font color="#ffc66d">set</font>(
            <font color="#46bd87">'name'</font>,
            <font color="#46bd87">'kora'</font> )</font
          >
          <br />
          <font color="#666"
            >person.<font color="#ffc66d">set</font>(
            <font color="#46bd87">'age'</font>,
            <font color="#46bd87">'25'</font> )</font
          >
          <br />
          <font color="#666"
            >person.<font color="#ffc66d">set</font>(
            <font color="#46bd87">'sex'</font>, person.<font color="#ffc66d"
              >get</font
            >( <font color="#46bd87">'name'</font> ) ==
            <font color="#46bd87">'kora'</font> &&
            <font color="#46bd87">'Mr'</font> )</font
          >
          <br />
          <font color="#666"
            >person.<font color="#ffc66d">set</font>(
            <font color="#46bd87">'say'</font>, () => { console.<font
              color="#ffc66d"
              >info</font
            >( <font color="#46bd87">'it's Kora'</font> ) })</font
          >
          <br />
          <br />
          <font color="#666"
            >person.<font color="ffc66d">get</font>(<font color="#46bd87"
              >'say'</font
            >)()</font
          >
          <br />
          <font
            style="
              margin-top: 30px;
              display: block;
              width: 100%;
              text-align: center;
              font-size: 16px;
            "
            >F12 + F5</font
          >
        </div>
      </div>

      <!--

      

      `data-``data-scale="4"`
      4
      4

  -->
      <div
        id="title"
        class="step"
        data-x="1500"
        data-y="1500"
        data-scale="4"
        style="color: #94e9ec"
      >
        <h2 style="color: #eee">
          
          <font color="#94e9ec" style="font-size: 60px; font-family: JasmineUPC"
            ></font
          >
        </h2>
        <span
          ><span class="animates" style="color: aqua"></span
          ></span
        >
        <div>...</div>
      </div>

      <div
        id="its"
        class="step"
        data-x="950"
        data-y="3000"
        data-rotate="90"
        data-scale="5"
      >
        <h2 style="color: #c1c1c1"></h2>
        <ul style="color: #c1c1c8">
          <li> </li>
          <li> </li>
          <li> </li>
        </ul>
      </div>

      <div
        id="imagination"
        class="step"
        data-x="6700"
        data-y="-300"
        data-scale="6"
      >
        <h2></h2>
        <ul>
          <li> vue</li>
          <li> PC: JQ/JS/H5/CSS</li>
          <li> /wepy/taro</li>
          <li> es6+/TS/SEO/node...</li>
        </ul>
      </div>

      <div
        id="source"
        class="step"
        data-x="6300"
        data-y="2000"
        data-rotate="20"
        data-scale="4"
        style="color: #6aa9ec"
      >
        <h2></h2>
        <ul>
          <li><a href="https://www.cuiqq.com/urging#/"></a></li>
          <li><a href="https://www.x315.com/"></a></li>
          <li><a href="https://www.vsigo.cn"></a></li>
          <li> <img src="../assets/img/dlyx.jpg" alt="" /></li>
          <li> <img src="../assets/img/xry.jpg" alt="" /></li>
          <li>...</li>
        </ul>
      </div>

      <div
        id="one-more-thing"
        class="step"
        data-x="6000"
        data-y="4000"
        data-scale="2"
      >
        <p>
          <span style="margin-right: 10px; font-size: 40px; color: #ff4466"
            > 
          </span>
          i wait you
        </p>
      </div>

      <!--

          impress.js

          3D3D
          X-4040Y10

          z`data-rotate-z``data-rotate`

      -->
      <div
        id="its-in-3d"
        class="step"
        data-x="6200"
        data-y="4400"
        data-z="-100"
        data-rotate-x="-40"
        data-rotate-y="20"
        data-scale="2"
      >
        <h3></h3>
        <div class="step_one">
          <div style="display: block; margin-bottom: 10px; color: #46bd87">
            <span style="color: #eee"></span>17865356192
          </div>
          <div style="display: block; margin-bottom: 10px; color: #46bd87">
            <span style="color: #eee"></span>keycode86@outlook.com
          </div>
          <div style="display: block; margin-bottom: 5px; color: #46bd87">
            <span style="color: #eee"></span><a
              href="https://kora-kr.github.io/"
              >https://kora-kr.github.io/</a
            >
          </div>

          <div style="width: 33%; float: left">
            <img src="../assets/img/wx.jpg" class="wx" alt="" />
            <p style="font-size: 20px; color: #eee"></p>
          </div>
          <div style="width: 33%; float: left">
            <img src="../assets/img/zfb.jpg" class="wx zfb" alt="" />
            <p style="font-size: 20px; color: #eee"></p>
          </div>
          <div style="width: 33%; float: left">
            <img src="../assets/img/gzh.jpg" class="wx zfb" alt="" />
            <p style="font-size: 20px; color: #eee"></p>
          </div>
        </div>
      </div>

      <!--

          

          * `data-x`, `data-y`, `data-z`  0
          * `data-rotate-x`, `data-rotate-y`, 'data-rotate-z`, `data-rotate`  
              0`data-rotate`  `data-rotate-z`
          * `data-scale`  1

          impress.jsCSStransformation
          https://developer.mozilla.org/en/CSS/transform

      -->
      <div
        id="overview"
        class="step"
        data-x="3000"
        data-y="1500"
        data-scale="10"
      ></div>
    </div>

    <!--

        `impress()` API

        

            var api = impress();

        

            `api.init()` - 
            `api.next()` - 
            `api.prev()` - 
            `api.goto( idx | id | element, [duration] )` - idDOM
                    

        `impress()`API`impress().next()`
        impress

    -->
  </section>
</template>

<script>
export default {
  name: "phpResume",
  props: [],
  computed: {},
  methods: {
    handleClickPrev() {
      impress().prev();
    },
    handleClickNext() {
      impress().next();
    },
    handleClickToHome() {
      this.$router.push({ name: "home", params: { flag: "back" } });
    },
  },
};
</script>

<style scoped>
.hint {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 20%;
  font-size: 0.8em;
  z-index: 999999 !important;
  color: #fff;
  opacity: 1 !important;
}
.hint > p {
  padding-bottom: 10px;
  margin-bottom: 0;
  box-sizing: border-box;
  border-bottom: 1px solid #eee;
}
.hint > div {
  display: flex;
  justify-content: space-around;
  align-items: center;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
}
.hint > div > div {
  line-height: 50px;
  transition: all 0.3s linear;
}
.hint > div > div:hover {
  color: #72b182;
  font-weight: 600;
}

.step_one {
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
  height: 400px;
  border: 1px solid #eee;
  background: #fff;
  box-shadow: 0 0 5px #eee;
  border-radius: 6px;
}

.animates {
  animation: kr 0.6s linear alternate infinite;
  position: relative;
  top: 0;
  left: 0;
}
@keyframes kr {
  from {
    top: -14px;
  }
  to {
    top: 0;
  }
}

li {
  position: relative;
  top: 0;
  left: 0;
  text-align: center;
  list-style: none;
  cursor: pointer;
}
li > img {
  position: absolute;
  top: 50%;
  right: 20%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: auto;
  opacity: 0;
  transition: all 0.3s linear;
}
li:hover > img {
  opacity: 1;
}

.wx {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 15%);
  width: 160px;
  height: 160px;
  border-radius: 10px;
  border: 1px solid #eee;
  box-shadow: 0 0 5px #eee;
}
</style>



